/* App.js uses this css file */
/* bootstrap link is in index.html in public folder of project */
#board{
  margin-top:3.5%;
  padding-left: 16%;
  display:inline-block;
}

#rectangle{
  padding:10%;
  outline:5px solid black;
  border-radius:2px;
}

#buttons{
  margin-top:1%;
  padding-left:2%;
  display:inline-block;
}

.sBtn{
  margin-right: 1%;
  width:200%;
  display:block;
  margin-top:5%;
  margin-bottom:5%;
}

.rBtn{
  margin-left: 0%;
  margin-right: 1%;
  width:200%;
  display:block;
  margin-top:5%;
  margin-bottom:5%;
}

.cBtn{
  width:200%;
  display:block;
  margin-top:5%;
}

.eBtn{
  width:100%;
  padding: 7px 10px 7px 10px;
}

.pBtn{
  width:100%;
  padding: 7px 10px 7px 10px;
}

#sbs-buttons{
  display:flex;
  margin-top:140.5%;
  text-align:right;
}

.square{ /*filled (standard) squares, i.e. blank squares in the original generated puzzles; values are fixed*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}

.emptySquare{ /*filled square but with some modifications to differentiate from filled squares*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.redSquare{
  background-color: #fccbc7;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.greenSquare{
  background-color: #c2ffd2;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  text-align: center;
  font-weight: bold;
}

#leftBorder{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 16%;
}

#topBorder{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 6.25%;
}

#bottomBorder{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 85%;
}

#rightBorder{
  border-left: 6px solid black;
  height: 495px;
  position:absolute;
  left:54%;
}

#verticalOne{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 28.5%;
}

#verticalTwo{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 41.5%;
}

#horizontalOne{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 32.5%;
}

#horizontalTwo{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 59%;
}

.hDivider{
  margin-right:10px;
}

/*css for removing the js default scrollwheel for input type numbers*/
input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}



